/**
 * Variables
 */
/**
 * globally variables for welcome page
 */
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeDown {
  0% {
    opacity: 0;
    margin-top: -20px;
  }
  100% {
    opacity: 1;
    margin-top: 0px;
  }
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    margin-top: 250px;
  }
  100% {
    opacity: 1;
    margin-top: 0px;
  }
}

#header {
  height: 85px;
  width: 100%;
  background: #000;
  padding-left: 0px;
  padding-right: 0px;
}

#header .brand-name a {
  color: #fff;
}

#header .menu-list {
  padding: 3rem;
  left: 0;
}

#header .menu-list .list-item {
  display: inline;
  margin-left: 1rem;
}

#header .menu-list .list-item a {
  color: #fff;
}

#footer {
  min-height: 200px;
  width: 100%;
  background: #000;
  color: #fff;
  padding: 5rem;
  font-size: 11px;
}

#footer .social-menu .list-item {
  list-style-type: none;
  margin-bottom: 1rem;
}

#footer .social-menu .list-item a {
  color: #fff;
  font-size: 15px;
  text-align: center;
}

#footer .sitemap .list-item {
  list-style-type: none;
  margin-bottom: 1rem;
}

#footer .sitemap .list-item a {
  color: #FFF;
  font-size: 15px;
  text-align: center;
}

.bottom-footer {
  height: 40px;
  width: 100%;
  background: #03A9F4;
  padding: 1rem;
  color: #FFF;
}

@keyframes color {
  0% {
    background: #06506B;
    color: #000;
  }
  25% {
    background: #F34C4C;
  }
  50% {
    background: #8eb4cb;
  }
  75% {
    background: #2ab27b;
  }
  100% {
    background: #fff;
  }
}

@keyframes reversecolor {
  0 {
    background: #fff;
  }
  25% {
    background: #2ab27b;
    color: #000;
  }
  50% {
    background: #8eb4cb;
  }
  75% {
    background: #F34C4C;
  }
  100% {
    background: #06506B;
  }
}

#layout {
  height: 100%;
  width: 100%;
  background: #DFEFF9;
  font-family: 'Raleway';
  @impport 'pages/about';
}

#layout .loader {
  height: 100%;
  width: 100%;
  background: #000;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0;
  bottom: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  padding-top: 300px;
}

#layout .loader .cp-meter {
  -webkit-animation: color 5s linear infinite;
  -moz-animation: color 5s linear infinite;
  -o-animation: color 5s linear infinite;
  animation: color 5s linear infinite;
}

#layout #header {
  height: 85px;
  width: 100%;
  background: #000;
