#layout #blog {
  height: 100%;
  width: 80%;
  padding: 2rem;
}

#layout #blog .post {
  padding: 2rem;
}

#layout #blog .post .post__cover:hover .overlay {
  display: block;
  transition: ease-in-out 0.2s all;
}

#layout #blog .post .post__cover .overlay {
  height: 250px;
  width: 97%;
  position: absolute;
  background: #06506B;
  opacity: .9;
  display: none;
  padding: 10rem;
}

#layout #blog .post .post__cover img {
  min-height: 250px;
  width: 100%;
}

#layout #head-section {
  min-height: 650px;
  width: 100%;
  background: url("../images/blogging-336376_1920.jpg");
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  font-family: helvetica;
  color: #FFF;
}

#layout #head-section .row.headline-row {
  height: auto;
  width: auto;
  margin-bottom: 250px;
  -webkit-animation: fadeDown 2s;
  -moz-animation: fadeDown 2s;
  -o-animation: fadeDown 2s;
  animation: fadeDown 2s;
}

#layout #head-section .row.information {
  height: auto;
  width: auto;
  -webkit-animation: fadeUp 2s;
  -moz-animation: fadeUp 2s;
  -o-animation: fadeUp 2s;
  animation: fadeUp 2s;
}

